<script>
import Loader from '@/components/Loader.vue';

export default {
    data() {
        return{
            loader: true,
            job: []
        }
    },
    created() {
        this.getJobs();
    },
    methods: {
        getJobs() {
            this.loader = true;

            this.$axios.get(`jobs/${this.$route.params.id}`)
            .then(res => {
                console.log(res.data);
                this.job = res.data;

                this.loader = false;
            })
            .catch(err => {
                this.$router.push('/vagas');
            })
        }
    },
    components: { Loader }
}
</script>

<template>
    <Loader v-if="loader" />

    <section v-if="!loader" class="bg-jobs">
        <figure>
            <img src="@/assets/img/instituto-aprender-de-qualificacao-logo.png" alt="">
        </figure>

        <header>
            <h2>Vaga Aberta!</h2>

            <p @click="this.$router.push('/vagas')">Ver todas as vagas</p>
        </header>

        <section class="jobs-infos">
            <section class="job-inf">
                <p>Título da vaga: <span>{{ job.title }}</span></p>
                <p>Empresa: <span>{{ job.user.name }}</span></p>
            </section>

            <section class="job-description">
                <p class="description-title">Descrição:</p>
                <p>{{ job.description }}</p>
            </section>

            <section class="job-contact">
                <p class="contact-title">Contato:</p>
                <p>{{ job.contact }}</p>
            </section>
        </section>
    </section>
</template>

<style scoped>
.jobs-infos {
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 30px;
    max-width: 1300px;
}

.jobs-infos .job-inf {
    width: 100%;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

