    color: #1E2245;
}

.bg-jobs {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 20px 20px 20px;
}

.bg-jobs figure img {
    width: 150px;
    max-width: 98%;
    margin-top: 30px;
}

.bg-jobs header {
    width: 100%;
    text-align: center;
    margin-top: 20px;
}

.bg-jobs header h2 {
    font-size: 20px;
    color: #1E2245;
    font-weight: 650;
}

.bg-jobs header p {
    font-size: 14px;
    color: #1E2245;
    font-weight: 400;
    margin-top: 10px;
    cursor: pointer;
    text-decoration: underline;
    transition: .2s;
}

.bg-jobs header p:hover {
    transition: .2s;
    color: #E73E19;
}

.bg-jobs .jobs-list {
    width: 100%;
    max-width: 1300px;
    display: grid;
